                          <span>D-30</span>
                      </div>
                  </div>
                  <div class="list_bullet half">
                      <strong class="title bold pt30 pb10"> </strong>
                      <ul>
                          <li v-for="(item, index) in couponBrands" :key="index">
                              {{ item.title }}
                          </li>
                      </ul>
                  </div>
                  <div class="list_bullet half">
                      <strong class="title bold pt30 pb10"></strong>
                      <ul>
                          <li>  </li>
                      </ul>
                  </div>
              </template>
          </div>
