  </div>
</template>

<script>
import ModalSkin from '@/components/ModalSkin'

export default {
  data () {
    return {
      modalTitle: '',
      modalContent: '',
      modalClass: '',

      // Coupon Brands
      couponBrands: [
          { title: ' ', checked: true },
          { title: ' ', checked: true },
          { title: ' ', checked: true },
          { title: ' ', checked: false },
          { title: ' ', checked: false },
          { title: '', checked: false },
          { title: '', checked: false },
          { title: '', checked: false },
          { title: '', checked: false },
          { title: ' ', checked: false },
          { title: ' ', checked: false },
          { title: ' ', checked: false },
          { title: '', checked: false },
      ],
    }
  },
  components: {
    ModalSkin,
  },
  methods: {
    openModal (name) {
      this.$modal.show(name)
      /* 23.03.27    body  */
      document.querySelector('body').classList.add('fixed')
    },
  },
}
</script>

